"use client";
import { useState, useEffect } from "react";
import { useSearchParams } from "next/navigation";

export default function LoginPage() {
  const searchParams = useSearchParams();
  const role = searchParams.get("role") || "student"; // default to student

  const [formData, setFormData] = useState({});
  const [loading, setLoading] = useState(false);
  const [fields, setFields] = useState([]);

  useEffect(() => {
    if (role === "admin") {
      setFields([
        { name: "admin_id", label: "Admin ID", type: "text" },
        { name: "password", label: "Password", type: "password" },
      ]);
    } else if (role === "lecturer") {
      setFields([
        { name: "lecturer_id", label: "Lecturer ID", type: "text" },
        { name: "password", label: "Password", type: "password" },
      ]);
    } else {
      setFields([
        { name: "matric_no", label: "Matric No", type: "text" },
        { name: "password", label: "Password", type: "password" },
      ]);
    }
  }, [role]);

  const handleChange = (e) => {
    setFormData((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);

    // simulate API call
    setTimeout(() => {
      console.log("Submitted:", { role, ...formData });
      setLoading(false);
    }, 2000);
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-bg text-text">
      <div className="w-full max-w-sm bg-surface shadow-md rounded-2xl p-6 border border-border">
        <h1 className="text-2xl font-bold mb-4 text-center">
          {role === "admin"
            ? "Admin Login"
            : role === "lecturer"
            ? "Lecturer Login"
            : "Student Login"}
        </h1>
        <form onSubmit={handleSubmit} className="space-y-4">
          {fields.map((field) => (
            <div key={field.name}>
              <label
                htmlFor={field.name}
                className="block text-sm font-medium mb-1 text-textMuted"
              >
                {field.label}
              </label>
              <input
                id={field.name}
                name={field.name}
                type={field.type}
                onChange={handleChange}
                required
                className="w-full px-3 py-2 border border-border rounded-lg bg-surfaceElevated text-text focus:outline-none focus:ring-2 focus:ring-brand"
              />
            </div>
          ))}

          <button
            type="submit"
            disabled={loading}
            className={`w-full py-2 rounded-lg text-white font-semibold transition-all duration-200 ${
              loading
                ? "bg-brand/70 cursor-not-allowed"
                : "bg-brand hover:bg-brand/90"
            }`}
          >
            {loading ? "Logging in..." : "Login"}
          </button>
        </form>
      </div>
    </div>
  );
}
